.container {
    display: flex;
    height: 100vh; /* Full viewport height */
    background-color: #f5f5f5; /* Light background for content */
}

/* Sidebar styling */
.sidebar {
    width: 80px; /* Sidebar width */
    background-color: white;
    border-right: 1px solid #e0e0e0; /* Light border for separation */
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px 0;
}

/* Logo styling */
.logo {
    font-size: 34px;
    font-weight: bolder;
    color: #3454d1; /* Blue color for "SY" */
    margin-bottom: 30px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

/* Navigation styling */
nav ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

nav ul li {
    margin-bottom: 20px;
}

nav ul li a {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    background-color: #e7f7f9; /* Light blue background for icons */
    border-radius: 8px;
    transition: background-color 0.3s ease;
    color: #00c4cc; /* Teal icon color */
    font-size: 18px;
    text-decoration: none;
}

nav ul li a:hover {
    background-color: #cceeff; /* Slightly darker blue on hover */
}

/* Specific icon size adjustment */
.fa-solid {
    font-size: 20px; /* Icon size */
}

